// path
Object.getVal = (obj, path, defaultVal) => {
  const paths = path.split('.');

  // 
  const val = paths.reduce((prev, item) => {
    if (prev === undefined || prev === null) {
      return prev;
    }
    return prev[item];
  }, obj);

  // undefined,
  if (val === undefined) {
    return defaultVal;
  }
  return val;
};



